class MedicalKnowledgeBase {
  constructor() {
    this.symptoms = {
      'chest pain': {
        causes: ['Heart attack', 'Angina', 'Acid reflux', 'Muscle strain', 'Anxiety'],
        urgency: 'high',
        tests: ['ECG', 'Chest X-ray', 'Blood tests (Troponin)', 'Stress test'],
        specialists: ['Cardiologist', 'Emergency Medicine'],
        advice: 'Seek immediate medical attention if severe, crushing, or accompanied by shortness of breath'
      },
      'headache': {
        causes: ['Tension', 'Migraine', 'Dehydration', 'High blood pressure', 'Sinusitis'],
        urgency: 'medium',
        tests: ['Blood pressure check', 'CT scan (if severe)', 'Blood tests'],
        specialists: ['Neurologist', 'General Physician'],
        advice: 'Track triggers, maintain hydration, consider stress management'
      },
      'fever': {
        causes: ['Viral infection', 'Bacterial infection', 'Inflammatory conditions'],
        urgency: 'medium',
        tests: ['Complete Blood Count', 'Blood culture', 'Urine test'],
        specialists: ['General Physician', 'Internal Medicine'],
        advice: 'Monitor temperature, stay hydrated, rest. Seek care if >101.3F or persistent'
      },
      'shortness of breath': {
        causes: ['Asthma', 'Heart problems', 'Lung infection', 'Anxiety', 'Anemia'],
        urgency: 'high',
        tests: ['Chest X-ray', 'ECG', 'Pulmonary function test', 'Blood oxygen levels'],
        specialists: ['Pulmonologist', 'Cardiologist'],
        advice: 'Seek immediate care if severe or sudden onset'
      },
      'abdominal pain': {
        causes: ['Gastritis', 'Appendicitis', 'Gallstones', 'Kidney stones', 'IBS'],
        urgency: 'medium',
        tests: ['Ultrasound', 'CT scan', 'Blood tests', 'Urine analysis'],
        specialists: ['Gastroenterologist', 'General Surgeon'],
        advice: 'Note location, timing, and associated symptoms'
      },
      'back pain': {
        causes: ['Muscle strain', 'Herniated disc', 'Arthritis', 'Kidney problems'],
        urgency: 'low',
        tests: ['X-ray', 'MRI', 'Blood tests'],
        specialists: ['Orthopedist', 'Physiotherapist'],
        advice: 'Apply ice/heat, gentle stretching, avoid prolonged bed rest'
      },
      'dizziness': {
        causes: ['Inner ear problems', 'Low blood pressure', 'Dehydration', 'Medication side effects'],
        urgency: 'medium',
        tests: ['Blood pressure check', 'Blood sugar test', 'Hearing test'],
        specialists: ['ENT specialist', 'Neurologist'],
        advice: 'Avoid sudden movements, stay hydrated, check medications'
      }
    };

    this.conditions = {
      'diabetes': {
        symptoms: ['Excessive thirst', 'Frequent urination', 'Fatigue', 'Blurred vision'],
        tests: ['HbA1c', 'Fasting glucose', 'Oral glucose tolerance test'],
        management: ['Diet control', 'Regular exercise', 'Blood sugar monitoring', 'Medication compliance'],
        specialists: ['Endocrinologist', 'Diabetologist']
      },
      'hypertension': {
        symptoms: ['Often no symptoms', 'Headaches', 'Dizziness', 'Chest pain'],
        tests: ['Blood pressure monitoring', 'ECG', 'Kidney function tests'],
        management: ['Low sodium diet', 'Regular exercise', 'Weight management', 'Stress reduction'],
        specialists: ['Cardiologist', 'Internal Medicine']
      },
      'asthma': {
        symptoms: ['Wheezing', 'Shortness of breath', 'Chest tightness', 'Coughing'],
        tests: ['Pulmonary function test', 'Peak flow measurement', 'Allergy tests'],
        management: ['Avoid triggers', 'Use inhalers as prescribed', 'Regular monitoring'],
        specialists: ['Pulmonologist', 'Allergist']
      }
    };

    this.medications = {
      'paracetamol': { uses: 'Pain relief, fever reduction', dosage: '500-1000mg every 4-6 hours', maxDaily: '4000mg' },
      'ibuprofen': { uses: 'Pain relief, inflammation', dosage: '200-400mg every 4-6 hours', maxDaily: '1200mg' },
      'aspirin': { uses: 'Pain relief, heart protection', dosage: '75-100mg daily for heart', maxDaily: '4000mg for pain' }
    };

    this.emergencyKeywords = [
      'chest pain', 'heart attack', 'stroke', 'difficulty breathing', 'severe bleeding',
      'unconscious', 'seizure', 'severe allergic reaction', 'poisoning', 'severe burns'
    ];
  }

  analyzeSymptom(symptom) {
    const lowerSymptom = symptom.toLowerCase();
    for (const [key, data] of Object.entries(this.symptoms)) {
      if (lowerSymptom.includes(key)) {
        return { symptom: key, ...data };
      }
    }
    return null;
  }

  getConditionInfo(condition) {
    const lowerCondition = condition.toLowerCase();
    for (const [key, data] of Object.entries(this.conditions)) {
      if (lowerCondition.includes(key)) {
        return { condition: key, ...data };
      }
    }
    return null;
  }

  isEmergency(query) {
    const lowerQuery = query.toLowerCase();
    return this.emergencyKeywords.some(keyword => lowerQuery.includes(keyword));
  }

  getMedicationInfo(medication) {
    const lowerMed = medication.toLowerCase();
    for (const [key, data] of Object.entries(this.medications)) {
      if (lowerMed.includes(key)) {
        return { medication: key, ...data };
      }
    }
    return null;
  }
}

export default new MedicalKnowledgeBase();