import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { Activity, Upload, MapPin, MessageCircle, Home } from 'lucide-react';

function Header() {
  const location = useLocation();

  const navItems = [
    { path: '/', icon: Home, label: 'Dashboard' },
    { path: '/upload', icon: Upload, label: 'Upload Report' },
    { path: '/facilities', icon: MapPin, label: 'Find Facilities' },
    { path: '/chat', icon: MessageCircle, label: 'AI Assistant' }
  ];

  return (
    <header className="bg-white shadow-lg">
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-16">
          <div className="flex items-center space-x-2">
            <Activity className="h-8 w-8 text-primary" />
            <h1 className="text-xl font-bold text-gray-800">CareConnect</h1>
          </div>
          
          <nav className="flex space-x-8">
            {navItems.map(({ path, icon: Icon, label }) => (
              <Link
                key={path}
                to={path}
                className={`flex items-center space-x-1 px-3 py-2 rounded-md text-sm font-medium transition-colors ${
                  location.pathname === path
                    ? 'text-primary bg-blue-50'
                    : 'text-gray-600 hover:text-primary hover:bg-gray-50'
                }`}
              >
                <Icon className="h-4 w-4" />
                <span>{label}</span>
              </Link>
            ))}
          </nav>
        </div>
      </div>
    </header>
  );
}

export default Header;