import React from 'react';
import { useHealth } from '../context/HealthContext';
import { FileText, Calendar, MapPin, TrendingUp, AlertTriangle } from 'lucide-react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

function Dashboard() {
  const { state } = useHealth();

  const healthData = [
    { month: 'Jan', score: 85 },
    { month: 'Feb', score: 82 },
    { month: 'Mar', score: 88 },
    { month: 'Apr', score: 90 },
    { month: 'May', score: 87 },
    { month: 'Jun', score: 92 }
  ];

  const stats = [
    { title: 'Reports Uploaded', value: state.reports.length, icon: FileText, color: 'bg-blue-500' },
    { title: 'Appointments', value: state.appointments.length, icon: Calendar, color: 'bg-green-500' },
    { title: 'Facilities Found', value: state.facilities.length, icon: MapPin, color: 'bg-purple-500' },
    { title: 'Health Score', value: '92%', icon: TrendingUp, color: 'bg-orange-500' }
  ];

  const recommendations = [
    { type: 'Blood Test', priority: 'High', reason: 'Based on recent symptoms' },
    { type: 'Cardiology Checkup', priority: 'Medium', reason: 'Routine screening due' },
    { type: 'Eye Examination', priority: 'Low', reason: 'Annual checkup recommended' }
  ];

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold text-gray-900">Health Dashboard</h1>
        <div className="text-sm text-gray-500">Last updated: {new Date().toLocaleDateString()}</div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {stats.map((stat, index) => (
          <div key={index} className="bg-white rounded-lg shadow p-6">
            <div className="flex items-center">
              <div className={`${stat.color} rounded-lg p-3`}>
                <stat.icon className="h-6 w-6 text-white" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">{stat.title}</p>
                <p className="text-2xl font-semibold text-gray-900">{stat.value}</p>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Health Trend Chart */}
      <div className="bg-white rounded-lg shadow p-6">
        <h2 className="text-xl font-semibold text-gray-900 mb-4">Health Score Trend</h2>
        <ResponsiveContainer width="100%" height={300}>
          <LineChart data={healthData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="month" />
            <YAxis />
            <Tooltip />
            <Line type="monotone" dataKey="score" stroke="#3B82F6" strokeWidth={2} />
          </LineChart>
        </ResponsiveContainer>
      </div>

      {/* Recommendations */}
      <div className="bg-white rounded-lg shadow p-6">
        <h2 className="text-xl font-semibold text-gray-900 mb-4">AI Recommendations</h2>
        <div className="space-y-4">
          {recommendations.map((rec, index) => (
            <div key={index} className="flex items-center justify-between p-4 border rounded-lg">
              <div className="flex items-center space-x-3">
                <AlertTriangle className={`h-5 w-5 ${
                  rec.priority === 'High' ? 'text-red-500' : 
                  rec.priority === 'Medium' ? 'text-yellow-500' : 'text-green-500'
                }`} />
                <div>
                  <p className="font-medium text-gray-900">{rec.type}</p>
                  <p className="text-sm text-gray-600">{rec.reason}</p>
                </div>
              </div>
              <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                rec.priority === 'High' ? 'bg-red-100 text-red-800' :
                rec.priority === 'Medium' ? 'bg-yellow-100 text-yellow-800' :
                'bg-green-100 text-green-800'
              }`}>
                {rec.priority}
              </span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Dashboard;