import React, { useState, useRef, useEffect } from 'react';
import { useHealth } from '../context/HealthContext';
import { Send, Bot, User, Loader, AlertTriangle, Heart, Activity } from 'lucide-react';
import medicalKB from '../utils/medicalKnowledgeBase';

function ChatBot() {
  const { state, dispatch } = useHealth();
  const [message, setMessage] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const messagesEndRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [state.chatHistory]);

  // Initialize with welcome message
  useEffect(() => {
    if (state.chatHistory.length === 0) {
      dispatch({
        type: 'ADD_CHAT_MESSAGE',
        payload: {
          id: Date.now(),
          type: 'bot',
          message: ' **Welcome to CareConnect AI Health Assistant!**\n\nI\'m here to provide comprehensive health support:\n\n **Symptom Analysis** - Get detailed analysis of your symptoms\n **Smart Report Analysis** - Upload & interpret health reports\n **Find Healthcare** - Locate hospitals, labs & specialists\n **Medication Guidance** - Drug information & interactions\n **Condition Information** - Learn about diseases & management\n\n**Try asking:**\n "I have a headache and fever"\n "What is diabetes?"\n "Find cardiologists near me"\n "Explain blood pressure readings"\n\nHow can I help with your health today?',
          timestamp: new Date().toISOString()
        }
      });
    }
  }, []);

  const getAIResponse = async (userMessage) => {
    await new Promise(resolve => setTimeout(resolve, 1000 + Math.random() * 1500));

    const lowerMessage = userMessage.toLowerCase();
    
    // Check for emergency situations first
    if (medicalKB.isEmergency(lowerMessage)) {
      return ` **EMERGENCY ALERT** \n\nThis sounds like a medical emergency. Please:\n Call emergency services (108/102) immediately\n Go to the nearest hospital\n Don't delay seeking immediate medical care\n\nIf this is not an emergency, please rephrase your question.`;
    }

    // Analyze symptoms
    const symptomAnalysis = medicalKB.analyzeSymptom(lowerMessage);
    if (symptomAnalysis) {
      const urgencyIcon = symptomAnalysis.urgency === 'high' ? '' : symptomAnalysis.urgency === 'medium' ? '' : '';
      return `${urgencyIcon} **${symptomAnalysis.symptom.toUpperCase()} Analysis**\n\n**Possible Causes:**\n${symptomAnalysis.causes.map(c => ` ${c}`).join('\n')}\n\n**Recommended Tests:**\n${symptomAnalysis.tests.map(t => ` ${t}`).join('\n')}\n\n**Specialists to Consult:**\n${symptomAnalysis.specialists.map(s => ` ${s}`).join('\n')}\n\n**Advice:** ${symptomAnalysis.advice}\n\n Would you like me to find nearby ${symptomAnalysis.specialists[0].toLowerCase()}s or diagnostic centers?`;
    }

    // Analyze medical conditions
    const conditionInfo = medicalKB.getConditionInfo(lowerMessage);
    if (conditionInfo) {
      return ` **${conditionInfo.condition.toUpperCase()} Information**\n\n**Common Symptoms:**\n${conditionInfo.symptoms.map(s => ` ${s}`).join('\n')}\n\n**Diagnostic Tests:**\n${conditionInfo.tests.map(t => ` ${t}`).join('\n')}\n\n**Management Strategies:**\n${conditionInfo.management.map(m => ` ${m}`).join('\n')}\n\n**Specialists:** ${conditionInfo.specialists.join(', ')}\n\n Would you like me to help you find specialists or diagnostic facilities for ${conditionInfo.condition}?`;
    }

    // Medication information
    const medicationInfo = medicalKB.getMedicationInfo(lowerMessage);
    if (medicationInfo) {
      return ` **${medicationInfo.medication.toUpperCase()} Information**\n\n**Uses:** ${medicationInfo.uses}\n**Typical Dosage:** ${medicationInfo.dosage}\n**Maximum Daily Dose:** ${medicationInfo.maxDaily}\n\n **Important:** Always consult a doctor or pharmacist before taking any medication. Dosages may vary based on individual conditions.`;
    }

    // Health report analysis
    if (lowerMessage.includes('report') || lowerMessage.includes('result')) {
      const userReports = state.reports.length;
      return ` **Health Report Analysis**\n\nI can help analyze your health reports! ${userReports > 0 ? `I see you have ${userReports} report(s) uploaded.` : 'You can upload reports in the Upload section.'}\n\n**I can help with:**\n Blood test interpretation\n Vital signs analysis\n Risk factor identification\n Recommendation generation\n\n${userReports > 0 ? 'Based on your uploaded reports, would you like me to explain any specific values or recommend follow-up tests?' : 'Upload a report and I\'ll provide detailed analysis and recommendations!'}`;
    }

    // Facility and appointment queries
    if (lowerMessage.includes('hospital') || lowerMessage.includes('doctor') || lowerMessage.includes('appointment')) {
      return ` **Medical Facility Assistance**\n\n**I can help you find:**\n Hospitals (Apollo, Fortis, Max, AIIMS)\n Diagnostic Labs (SRL, Dr Lal PathLabs, Metropolis)\n Specialist Clinics\n Government Healthcare Centers\n\n**Search Options:**\n By specialty (Cardiology, Neurology, etc.)\n By location and distance\n By cost range (50 - 1200)\n By ratings and reviews\n\n **Cost Comparison Available!**\nWould you like me to show you facilities for a specific specialty or condition?`;
    }

    // General health queries
    if (lowerMessage.includes('health') || lowerMessage.includes('wellness')) {
      return ` **General Health Guidance**\n\n**Preventive Care Tips:**\n Regular health checkups (annual)\n Maintain healthy BMI (18.5-24.9)\n Exercise 150 minutes/week\n Balanced diet with fruits & vegetables\n Adequate sleep (7-9 hours)\n Stress management\n Avoid smoking and limit alcohol\n\n**When to See a Doctor:**\n Persistent symptoms > 2 weeks\n Sudden severe symptoms\n Changes in existing conditions\n Preventive screenings\n\nWhat specific aspect of health would you like to discuss?`;
    }

    // Test and lab queries
    if (lowerMessage.includes('test') || lowerMessage.includes('lab') || lowerMessage.includes('blood')) {
      return ` **Medical Tests & Diagnostics**\n\n**Common Tests & Costs:**\n Complete Blood Count (CBC): 200-400\n Lipid Profile: 300-500\n HbA1c (Diabetes): 250-450\n Thyroid Function: 400-600\n Liver Function: 300-500\n Kidney Function: 250-400\n\n**Imaging Tests:**\n X-Ray: 150-400\n Ultrasound: 800-1500\n CT Scan: 3000-8000\n MRI: 5000-15000\n\n **Available at:** SRL, Dr Lal PathLabs, Metropolis, Thyrocare\n\nWhich specific test are you interested in, or shall I recommend tests based on your symptoms?`;
    }

    // Default intelligent response
    return ` **Hello! I'm your AI Health Assistant**\n\n**I can help you with:**\n\n **Symptom Analysis** - Describe your symptoms for detailed analysis\n **Report Interpretation** - Upload reports for AI analysis\n **Find Healthcare** - Locate hospitals, labs, specialists\n **Medication Info** - Drug information and interactions\n **Health Conditions** - Information about diseases and management\n **Cost Comparison** - Compare prices across facilities\n\n**Example queries:**\n "I have chest pain and shortness of breath"\n "What does high cholesterol mean?"\n "Find cardiologists near me"\n "Explain my blood test results"\n\n **Disclaimer:** I provide information for educational purposes. Always consult healthcare professionals for medical decisions.\n\nWhat would you like to know about your health today?`;
  };

  const handleSendMessage = async () => {
    if (!message.trim()) return;

    // Add user message
    const userMessage = {
      id: Date.now(),
      type: 'user',
      message: message.trim(),
      timestamp: new Date().toISOString()
    };
    dispatch({ type: 'ADD_CHAT_MESSAGE', payload: userMessage });

    setMessage('');
    setIsTyping(true);

    try {
      const response = await getAIResponse(message);
      
      // Add bot response
      const botMessage = {
        id: Date.now() + 1,
        type: 'bot',
        message: response,
        timestamp: new Date().toISOString()
      };
      dispatch({ type: 'ADD_CHAT_MESSAGE', payload: botMessage });
    } catch (error) {
      console.error('Chat error:', error);
      const errorMessage = {
        id: Date.now() + 1,
        type: 'bot',
        message: 'I apologize, but I\'m having trouble processing your request right now. Please try again later.',
        timestamp: new Date().toISOString()
      };
      dispatch({ type: 'ADD_CHAT_MESSAGE', payload: errorMessage });
    }

    setIsTyping(false);
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  return (
    <div className="max-w-4xl mx-auto">
      <h1 className="text-3xl font-bold text-gray-900 mb-6">AI Health Assistant</h1>
      
      <div className="bg-white rounded-lg shadow-lg h-96 flex flex-col">
        {/* Chat Messages */}
        <div className="flex-1 overflow-y-auto p-4 space-y-4 chat-container">
          {state.chatHistory.map((msg) => (
            <div key={msg.id} className={`flex ${msg.type === 'user' ? 'justify-end' : 'justify-start'}`}>
              <div className={`flex items-start space-x-2 max-w-xs lg:max-w-md ${msg.type === 'user' ? 'flex-row-reverse space-x-reverse' : ''}`}>
                <div className={`flex-shrink-0 w-8 h-8 rounded-full flex items-center justify-center ${
                  msg.type === 'user' ? 'bg-primary' : 'bg-secondary'
                }`}>
                  {msg.type === 'user' ? (
                    <User className="h-4 w-4 text-white" />
                  ) : (
                    <Bot className="h-4 w-4 text-white" />
                  )}
                </div>
                <div className={`px-4 py-2 rounded-lg ${
                  msg.type === 'user' 
                    ? 'bg-primary text-white' 
                    : 'bg-gray-100 text-gray-900'
                }`}>
                  <div className="text-sm whitespace-pre-wrap" 
                       dangerouslySetInnerHTML={{
                         __html: msg.message
                           .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
                           .replace(/ (.*?)\n/g, '<div style="margin-left: 12px;"> $1</div>')
                           .replace(/\n/g, '<br/>')
                       }} 
                  />
                  <p className="text-xs opacity-70 mt-1">
                    {new Date(msg.timestamp).toLocaleTimeString()}
                  </p>
                </div>
              </div>
            </div>
          ))}
          
          {isTyping && (
            <div className="flex justify-start">
              <div className="flex items-start space-x-2 max-w-xs lg:max-w-md">
                <div className="flex-shrink-0 w-8 h-8 rounded-full bg-secondary flex items-center justify-center">
                  <Bot className="h-4 w-4 text-white" />
                </div>
                <div className="px-4 py-2 rounded-lg bg-gray-100">
                  <div className="flex items-center space-x-1">
                    <Loader className="h-4 w-4 animate-spin" />
                    <span className="text-sm text-gray-600">AI is thinking...</span>
                  </div>
                </div>
              </div>
            </div>
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* Message Input */}
        <div className="border-t p-4">
          <div className="flex space-x-2">
            <textarea
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder="Describe symptoms, ask about conditions, or request facility recommendations..."
              className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-primary focus:border-transparent resize-none"
              rows="2"
            />
            <button
              onClick={handleSendMessage}
              disabled={!message.trim() || isTyping}
              className="px-4 py-2 bg-primary text-white rounded-md hover:bg-blue-600 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              <Send className="h-4 w-4" />
            </button>
          </div>
          <p className="text-xs text-gray-500 mt-2">
             **Examples:** "I have chest pain and dizziness" | "What is hypertension?" | "Find cardiologists in Delhi" | "Explain my blood test"
          </p>
        </div>
      </div>
    </div>
  );
}

export default ChatBot;