import React, { useState } from 'react';
import { useHealth } from '../context/HealthContext';
import { Upload, FileText, Eye, Loader } from 'lucide-react';
import { createWorker } from 'tesseract.js';

function ReportUpload() {
  const { dispatch } = useHealth();
  const [file, setFile] = useState(null);
  const [preview, setPreview] = useState(null);
  const [extractedText, setExtractedText] = useState('');
  const [analysis, setAnalysis] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleFileSelect = (event) => {
    const selectedFile = event.target.files[0];
    if (selectedFile) {
      setFile(selectedFile);
      const reader = new FileReader();
      reader.onload = (e) => setPreview(e.target.result);
      reader.readAsDataURL(selectedFile);
    }
  };

  const handleDrop = (event) => {
    event.preventDefault();
    const droppedFile = event.dataTransfer.files[0];
    if (droppedFile) {
      setFile(droppedFile);
      const reader = new FileReader();
      reader.onload = (e) => setPreview(e.target.result);
      reader.readAsDataURL(droppedFile);
    }
  };

  const extractTextFromImage = async () => {
    if (!file) return;
    
    setLoading(true);
    try {
      const worker = await createWorker();
      const { data: { text } } = await worker.recognize(file);
      setExtractedText(text);
      await worker.terminate();
      
      // Simulate AI analysis
      const mockAnalysis = {
        keyFindings: ['Blood pressure: 140/90 mmHg', 'Cholesterol: 220 mg/dL', 'Blood sugar: 110 mg/dL'],
        riskFactors: ['Hypertension', 'High cholesterol'],
        recommendations: ['Consult cardiologist', 'Dietary changes recommended', 'Regular exercise']
      };
      setAnalysis(mockAnalysis);
      
      // Add to context
      dispatch({
        type: 'ADD_REPORT',
        payload: {
          id: Date.now(),
          filename: file.name,
          uploadDate: new Date().toISOString(),
          extractedText: text,
          analysis: mockAnalysis
        }
      });
    } catch (error) {
      console.error('OCR Error:', error);
    }
    setLoading(false);
  };

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      <h1 className="text-3xl font-bold text-gray-900">Upload Health Report</h1>
      
      {/* Upload Area */}
      <div className="bg-white rounded-lg shadow p-6">
        <div
          className="upload-area rounded-lg p-8 text-center"
          onDrop={handleDrop}
          onDragOver={(e) => e.preventDefault()}
        >
          <Upload className="h-12 w-12 text-gray-400 mx-auto mb-4" />
          <p className="text-lg font-medium text-gray-900 mb-2">
            Drop your health report here or click to browse
          </p>
          <p className="text-sm text-gray-600 mb-4">
            Supports JPG, PNG, PDF files up to 10MB
          </p>
          <input
            type="file"
            accept="image/*,.pdf"
            onChange={handleFileSelect}
            className="hidden"
            id="file-upload"
          />
          <label
            htmlFor="file-upload"
            className="inline-flex items-center px-4 py-2 bg-primary text-white rounded-md hover:bg-blue-600 cursor-pointer"
          >
            <FileText className="h-4 w-4 mr-2" />
            Choose File
          </label>
        </div>
      </div>

      {/* Preview and Analysis */}
      {preview && (
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Image Preview */}
          <div className="bg-white rounded-lg shadow p-6">
            <h2 className="text-xl font-semibold text-gray-900 mb-4">Preview</h2>
            <img src={preview} alt="Report preview" className="w-full rounded-lg" />
            <button
              onClick={extractTextFromImage}
              disabled={loading}
              className="mt-4 w-full flex items-center justify-center px-4 py-2 bg-secondary text-white rounded-md hover:bg-green-600 disabled:opacity-50"
            >
              {loading ? (
                <Loader className="h-4 w-4 mr-2 animate-spin" />
              ) : (
                <Eye className="h-4 w-4 mr-2" />
              )}
              {loading ? 'Analyzing...' : 'Analyze Report'}
            </button>
          </div>

          {/* Analysis Results */}
          <div className="bg-white rounded-lg shadow p-6">
            <h2 className="text-xl font-semibold text-gray-900 mb-4">AI Analysis</h2>
            
            {extractedText && (
              <div className="mb-6">
                <h3 className="font-medium text-gray-900 mb-2">Extracted Text</h3>
                <div className="bg-gray-50 p-3 rounded text-sm max-h-32 overflow-y-auto">
                  {extractedText}
                </div>
              </div>
            )}

            {analysis && (
              <div className="space-y-4">
                <div>
                  <h3 className="font-medium text-gray-900 mb-2">Key Findings</h3>
                  <ul className="list-disc list-inside text-sm text-gray-600 space-y-1">
                    {analysis.keyFindings.map((finding, index) => (
                      <li key={index}>{finding}</li>
                    ))}
                  </ul>
                </div>

                <div>
                  <h3 className="font-medium text-gray-900 mb-2">Risk Factors</h3>
                  <div className="flex flex-wrap gap-2">
                    {analysis.riskFactors.map((risk, index) => (
                      <span key={index} className="px-2 py-1 bg-red-100 text-red-800 rounded-full text-xs">
                        {risk}
                      </span>
                    ))}
                  </div>
                </div>

                <div>
                  <h3 className="font-medium text-gray-900 mb-2">Recommendations</h3>
                  <ul className="list-disc list-inside text-sm text-gray-600 space-y-1">
                    {analysis.recommendations.map((rec, index) => (
                      <li key={index}>{rec}</li>
                    ))}
                  </ul>
                </div>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default ReportUpload;